@import url("https://fonts.googleapis.com/css?family=Merriweather:100,200,300,300i,400,400i,700");
@import url("https://fonts.googleapis.com/css?family=Roboto+Slab:400,700");
@import url("https://fonts.googleapis.com/css?family=Open+Sans:300,400");

body {
  font-family: "Open Sans", serif;
}

.wrapper {
  background: linear-gradient(to right, #e67e22, #e74c3c);
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.main {
  height: 90vh;
  background: #fff;
  box-shadow: 0px 13px 40px -13px rgba(0, 0, 0, 0.75);
  width: 80%;
  margin: 0 auto;
}

.form-container {
  flex: 1;
  background-image: url("img/bg.jpg");
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: #000;
  padding: 20px 30px;
  display: flex;
  flex-direction: column;
}

.form-container__title {
  font-size: 50px;
  letter-spacing: 2px;
  line-height: 1.3;
  font-family: "Roboto Slab", serif;
  padding-bottom: 1rem;
}

.weather-container {
  flex: 1.3 1;
  padding: 40px 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

input[type="text"] {
  background-color: transparent;
  border: 0;
  border-bottom: solid 1px #f16051;
  width: 100%;
  padding-bottom: 4px;
  color: #fff !important;
  font-weight: lighter;
  letter-spacing: 2px;
  margin-bottom: 30px;
  margin-right: 20px;
  font-size: 20px;
}

input[type="text"] {
  outline: none;
}

input:-webkit-autofill {
  -webkit-box-shadow: none;
}

button {
  border: 0;
  padding: 8px 20px;
  margin: 0 2px;
  border-radius: 2px;
  font-weight: lighter;
  letter-spacing: 1px;
  font-size: 15px;
  cursor: pointer;
  background-color: #f16051;
  color: #fff;
  font-weight: 100;
}

button:active {
  outline: none;
}

.weather__icon {
  background-position: center center;
  background-size: cover;
  width: 80px;
  height: 80px;
  align-self: center;
  margin-bottom: 1rem;
}

.weather__icon--01d,
.weather__icon--01n  {
  background-image: url("img/icons/01_clear_sky.png");
}

.weather__icon--02d,
.weather__icon--02n  {
  background-image: url("img/icons/02_few_clouds.png");
}

.weather__icon--03d,
.weather__icon--03n {
  background-image: url("img/icons/02_few_clouds.png");
}

.weather__icon--04d,
.weather__icon--04n {
  background-image: url("img/icons/04_broken_clouds.png");
}

.weather__icon--09d,
.weather__icon--09n  {
  background-image: url("img/icons/09_shower_rain.png");
}

.weather__icon--10d,
.weather__icon--10n {
  background-image: url("img/icons/10_heavy_rain.png");
}

.weather__icon--11d,
.weather__icon--11n {
  background-image: url("img/icons/11_thunderstorm.png");
}

.weather__icon--13d,
.weather__icon--13n {
  background-image: url("img/icons/13_snow.png");
}

.weather__icon--50d, 
.weather__icon--50n {
  background-image: url("img/icons/50_mist.png");
}

.weather__info {
  font-size: 20px;
  font-weight: 200;
  letter-spacing: 2px;
  display: flex;
  flex-direction: column;
}

.weather__key {
  color: #817574;
  border-bottom: solid 2px #f16051;
  padding: 20px 0;
  font-weight: 400;
}

.weather__key:last-child {
  border: 0;
}

.weather__value {
  color: black;
}

.weather__error {
  color: #f16051;
  font-size: 20px;
  letter-spacing: 1px;
  font-weight: 200;
}

@media screen and (max-width: 992px) {
  .main {
    width: 90%;
  }

  .form-container__title {
    font-size: 1.5rem;
  }

  input[type="text"] {
    font-size: 0.9rem;
  }

  button {
    font-size: 0.8rem;
  }

  .weather-container {
    padding: 0 30px;
  }

  .weather__info {
    font-size: 0.9rem;
    width: 100%;
  }

  .weather__key {
    color: #817574;
    border-bottom: solid 2px #f16051;
    padding: 10px 0;
    font-weight: 400;
  }

  .weather__icon {
    width: 60px;
    height: 60px;
  }

  .weather__error {
    padding-top: 1rem;
    font-size: 1.2rem;
    text-align: center;
  }
}
